import React, { Component } from "react";

export default class ShortenForm extends Component {
  state = { longUrl: "" };

  onShortenClick = () => {
    if (this.state.longUrl !== "") {
      this.props.onShorten(this.state.longUrl);
    }
  };

  render() {
    return (
      <div className='container'>
        <label className='label'>Paste the URL to be shortened</label>
        <div className='field is-grouped'>
          <div className='control is-expanded'>
            <input
              className='input'
              type='url'
              placeholder='e.g. https://www.thitiphon.me'
              onChange={(e) => this.setState({ longUrl: e.target.value })}
              value={this.state.longUrl}
            />
          </div>
          <div className='control'>
            <button className='button is-info' onClick={this.onShortenClick}>
              Shorten!
            </button>
          </div>
        </div>
      </div>
    );
  }
}
