import React, { Component } from "react";
import axios from "axios";
import ShortenForm from "./ShortenForm";
import ShortenOutput from "./ShortenOutput";
import "./App.css";

export default class App extends Component {
  state = { payload: null };

  onShorten = async (longUrl) => {
    const response = await axios.post(
      "https://asia-east2-thitiph0n-url-shortener.cloudfunctions.net/api/shorten",
      {
        longUrl: longUrl,
      }
    );
    this.setState({ payload: response.data });
  };

  render() {
    return (
      <div>
        <section className='hero'>
          <div className='logo hero-body'>
            <div className='container'>
              <h1 className='title'>Thitiphon.me</h1>
              <h2 className='subtitle'>URL Shortener Service</h2>
            </div>
          </div>
        </section>
        <section className='hero'>
          <div className='hero-body'>
            <ShortenForm onShorten={this.onShorten} />
            <br />
            <ShortenOutput payload={this.state.payload} />
          </div>
        </section>
      </div>
    );
  }
}
